body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#ceffe9}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.stock-figure-wrapper{padding-top:3%}.header-section{background-color:#00203FFF;height:64px;width:100%;top:0px;left:0px;z-index:999;margin-bottom:3%}.header-section .MuiGrid-container{height:64px}.header-section .MuiGrid-container .title-container{height:64px;padding:0 !important}.header-section .MuiGrid-container .title-container .title{position:absolute;color:white;margin:auto;left:4%;top:8px}.header-section .MuiGrid-container .subtitle-container{position:relative;height:64px;padding:0 !important}.header-section .MuiGrid-container .subtitle-container .subtitle-version{z-index:1000;position:fixed;color:#00203FFF;font-size:small;right:5px;bottom:20px;margin-bottom:5px}.header-section .MuiGrid-container .subtitle-container .subtitle-update-date{z-index:1000;position:fixed;color:#00203FFF;font-size:small;right:5px;bottom:5px;margin-bottom:5px}.header-section .MuiGrid-container .ticker-input-section{height:64px;padding:0 !important;background-color:#00203FFF}.header-section .MuiGrid-container .ticker-input-section form .MuiFormControl-root{float:right;margin-right:15%}.header-section .MuiGrid-container .ticker-input-section form .MuiFormControl-root .MuiInputBase-root{border-radius:5px;background-color:white;height:30px;margin-top:23px}.header-section .MuiGrid-container .ticker-input-section form .MuiFormControl-root .MuiInputBase-root input{min-width:50px;border-radius:5px;max-width:100px;padding-left:5px}

